import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";
import Auth from "@/views/Auth.vue";
import store from "@/store";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: {
      layout: "DesktopLayout",
    },
  },
  {
    path: "/character",
    name: "Character",
    component: () =>
      import(/* webpackChunkName: "character" */ "../views/Character.vue"),
    meta: {
      layout: "DesktopLayout",
    },
  },
  {
    path: "/admin",
    name: "AdminPanel",
    component: () =>
      import(/* webpackChunkName: "character" */ "../views/AdminPanel.vue"),
    meta: {
      layout: "DesktopLayout",
    },
  },
  {
    path: "/auth",
    name: "Auth",
    component: Auth,
    meta: {
      layout: "DOSLayout",
      isPublic: true,
    },
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

//Side effects
router.beforeEach((to) => {
  if (to.query.code) store.dispatch("userAuth", to.query.code);
});

//Guards and redirections
router.beforeEach((to, from, next) => {
  if (!to.meta.isPublic && !store.state.isAuthorized) next({ name: "Auth" });
  else if (to.name === "Auth" && store.state.isAuthorized)
    next({ name: "Home" });
  else next();
});

export default router;
