<template>
  <div class="panel create-character p-2">
    <div class="row">
      <div class="col">
        <div class="field-row-stacked">
          <label>Имя сущности</label>
          <input type="text" placeholder="Marduk" v-model="formData.name" />
        </div>
        <div class="field-row-stacked">
          <label>Имя личности</label>
          <input
            type="text"
            placeholder="Мардук"
            v-model="formData.display_name"
          />
        </div>
      </div>
      <div class="col">
        <div class="field-row-stacked">
          <label>Пароль</label>
          <input type="password" v-model="formData.password" />
        </div>
        <div class="field-row-stacked">
          <label>Повторите пароль</label>
          <input type="password" v-model="passwordConfirm" />
        </div>
      </div>
    </div>
    <button class="mt-2" :disabled="!isCanProceed" @click="handleSubmit">
      Завершить
    </button>
  </div>
</template>

<script>
import { createRequest } from "@/api/requestBuilder";
import requestConfigs from "@/api/requestConfigs";

export default {
  name: "CreateCharacterForm",
  data() {
    return {
      formData: {
        name: "",
        password: "",
        display_name: "",
      },
      passwordConfirm: "",
    };
  },
  emits: ["creation-completed"],
  computed: {
    isCanProceed() {
      return (
        this.formData.name &&
        this.formData.password &&
        this.formData.display_name &&
        this.passwordConfirm
      );
    },
  },
  methods: {
    handleSubmit() {
      if (!this.isCanProceed) return;
      if (this.formData.password !== this.passwordConfirm) {
        this.$store.commit(
          "pushToTray",
          "Пароль и подтверждение пароля не совпадают."
        );
        return;
      }

      createRequest(requestConfigs.POSTCreateCharacter, {
        jsonPayload: this.formData,
      })
        .then(() => {
          this.$store.commit("pushToTray", "Сущность успешно проявлена.");
          this.$emit("creation-completed");
        })
        .catch(() => {
          this.$store.commit("pushToTray", "Что-то пошло не так");
        });
    },
  },
};
</script>

<style lang="scss"></style>
