<template>
  <div
    class="popup-shell"
    ref="popupContainer"
    :style="{ left: popupPosition.x + 'px', top: popupPosition.y + 'px' }"
  >
    <Window
      :title="popupData.title"
      :show-button-close="true"
      :is-draggable="true"
      v-on:close-click="closePopup"
      v-on:mousedown-on-title="
        () => {
          this.isPopupDragging = true;
        }
      "
      v-on:mouseup-on-title="
        () => {
          this.isPopupDragging = false;
        }
      "
      v-on:mousemove-on-title="dragPopup"
      v-on:mouseout="
        () => {
          this.isPopupDragging = false;
        }
      "
    >
      <component :is="popupData.component" :popupData="popupData" />
    </Window>
  </div>
</template>

<script>
import Window from "@/components/Window";
import { defineAsyncComponent } from "vue";

export default {
  name: "PopupShell",
  components: {
    Window,
    ItemPopup: defineAsyncComponent(() =>
      import("@/components/PopupSections/Popups/ItemPopup/index.vue")
    ),
    AbilityPopup: defineAsyncComponent(() =>
      import("@/components/PopupSections/Popups/AbilityPopup")
    ),
  },
  props: {
    popupData: {
      type: Object,
    },
  },
  data() {
    return {
      isPopupDragging: false,
      popupPosition: { x: 0, y: 0 },
    };
  },
  computed: {
    containerSize() {
      return {
        height: Math.max(this.$refs.popupContainer.offsetHeight, 300),
        width: Math.max(this.$refs.popupContainer.offsetWidth, 500),
      };
    },
  },
  methods: {
    closePopup() {
      this.$store.commit("removePopup", this.popupData.id);
    },
    dragPopup(event) {
      if (!this.isPopupDragging) return;
      this.popupPosition.x += event.movementX;
      this.popupPosition.y += event.movementY;
    },
  },
  mounted() {
    const windowShift = 10 * this.$store.state.activePopups.length;
    this.popupPosition = {
      x: window.innerWidth / 2 - this.containerSize.width / 2 + windowShift,
      y: window.innerHeight / 2 - this.containerSize.height / 2 + windowShift,
    };
  },
};
</script>

<style lang="scss">
.popup-shell {
  position: fixed;
  z-index: 10;
  text-align: left;
}
</style>
