<template>
  <div class="tray-item">
    <div class="icon">
      <Icon icon="flat-color-icons:advertising" />
    </div>
    <div>{{ itemData.text }}</div>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue";

export default {
  name: "TrayItem",
  components: { Icon },
  data() {
    return {
      timeout: null,
    };
  },
  props: {
    itemData: {
      type: Object,
    },
  },
  mounted() {
    this.timeout = setTimeout(
      () => this.$store.commit("removeFromTray", this.itemData.id),
      4000
    );
  },
};
</script>

<style lang="scss">
.tray-item {
  background-color: #fff3ae;
  border: 2px dashed black;
  padding: 10px;
  margin: 5px;
  width: 200px;
  text-align: left;
  display: flex;

  .icon {
    line-height: 0;
    font-size: 40px;
    margin-right: 10px;
    display: flex;
    align-items: center;
  }
}
</style>
