<template>
  <div class="mc-face-viewer-8x" :style="{ backgroundImage: skinCSSProp }" />
</template>

<script>
export default {
  name: "SkinFaceViewer",
  props: {
    skinLink: {
      type: String,
      default: "",
    },
  },
  computed: {
    skinCSSProp() {
      return `url("${this.skinLink}")`;
    },
  },
};
</script>

<style lang="scss">
@import "~@/assets/scss/skinViewer/mc-skin-viewer.scss";
</style>
