import { uniqueString } from "@/helpers/helpersList";

export function dumbMutation(state, { field, value }) {
  state[field] = value;
}

export function setUserAuthData(state, data) {
  state.userAuthData = data;
}
export function pushToTray(state, text) {
  state.trayItems.push({
    text,
    id: uniqueString(),
  });
}
export function removeFromTray(state, id) {
  let indexToRemove = state.trayItems.findIndex((item) => item.id === id);
  state.trayItems.splice(indexToRemove, 1);
}
export function addPopup(state, data) {
  state.activePopups.push({
    id: uniqueString(),
    component: data.component,
    title: data.title,
    params: data.params,
  });
}
export function removePopup(state, id) {
  let indexToRemove = state.activePopups.findIndex((item) => item.id === id);
  state.activePopups.splice(indexToRemove, 1);
}
