import { createRequest } from "@/api/requestBuilder";
import requestConfigs from "@/api/requestConfigs";
import router from "@/router";

export function userAuth({ commit }, discordAuthToken) {
  return new Promise((resolve, reject) => {
    createRequest(requestConfigs.POSTCheckToken, {
      jsonPayload: { discordCode: discordAuthToken },
    })
      .then((result) => {
        commit("setUserAuthData", result.data);
        commit("dumbMutation", { field: "isAuthorized", value: true });
        router.push({ name: "Home" });
        resolve(result);
      })
      .catch((err) => {
        reject(err);
      })
  });
}
