<template>
  <div class="character-select-panel">
    <header>Выбор пользователя</header>
    <div class="character-list" v-if="charactersList.length > 0">
      <div
        class="character"
        v-for="(item, index) in charactersList"
        :key="index"
        @click="selectCharacter(item.name)"
      >
        <SkinFaceViewer class="bordered face" :skin-link="item.skin" />
        <div class="name">{{ item.display_name }}</div>
      </div>
    </div>
    <div v-else class="character-list_empty infosphere bordered">
      <div class="error">ОШИБКА: СУЩНОСТИ НЕ НАЙДЕНЫ</div>
    </div>
    <div
      class="character-create-container"
      :class="{ panel: isCreateCharacterForm }"
    >
      <button
        v-if="!isCreateCharacterForm"
        @click="isCreateCharacterForm = true"
      >
        Проявить сущность
      </button>
      <button v-if="!isCreateCharacterForm" class="ms-2" @click="logOut()">
        Завершение работы
      </button>
      <CreateCharacterForm
        v-else
        @creation-completed="
          () => {
            this.isCreateCharacterForm = false;
            this.loadCharacters();
          }
        "
      />
    </div>
  </div>
</template>

<script>
import SkinFaceViewer from "@/components/SkinFaceViewer";
import { createRequest } from "@/api/requestBuilder";
import requestConfigs from "@/api/requestConfigs";
import CreateCharacterForm from "@/components/CreateCharacterForm";
export default {
  name: "CharacterSelectPanel",
  components: { CreateCharacterForm, SkinFaceViewer },
  data() {
    return {
      charactersList: [],
      isCreateCharacterForm: false,
    };
  },
  beforeMount() {
    this.loadCharacters();
  },
  methods: {
    loadCharacters() {
      createRequest(requestConfigs.GETCharacterList).then((result) => {
        this.charactersList = result.data;
      });
    },
    selectCharacter(name) {
      this.$store.commit("dumbMutation", {
        field: "selectedCharacterName",
        value: name,
      });
    },
    logOut() {
      this.$store.commit("dumbMutation", {
        field: "isAuthorized",
        value: false,
      });
      this.$router.push({ name: "Auth" });
    },
  },
};
</script>

<style lang="scss">
.character-select-panel {
  flex: 2;
  display: flex;
  flex-direction: column;
  align-items: center;

  header {
    font-family: "Press Start 2P", cursive;
    color: white;
    font-size: 20px;
    margin-bottom: 15px;
  }

  .character-list_empty {
    font-family: "Press Start 2P", cursive;
    color: white;
    font-size: 15px;
    margin: 30px auto 0 auto;
    padding: 40px;
    display: flex;
    flex-direction: column;

    .error {
      animation: float linear 4s infinite;
    }
  }

  .character-create-container {
    margin-top: 20px;
  }

  .character-list {
    padding: 20px;
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

  .character {
    display: flex;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    width: 100px;
    height: 100px;

    &:hover {
      transform: translateY(-10px);
    }

    .face {
      margin-bottom: 15px;
      flex-shrink: 0;
    }
    .name {
      color: white;
      font-family: "Press Start 2P", cursive;
      font-size: 13px;
    }
  }
}
</style>
