export default {
  GETEvents: {
    url: "/events",
    type: "get",
  },
  GETDiscordLink: {
    url: "/login/discord",
    type: "get",
    queryKeys: ["redirect_url"],
  },
  POSTCheckToken: {
    url: "/token/discord",
    type: "post",
    jsonKeys: ["discordCode"],
  },
  POSTLogIn: {
    url: "/login",
    type: "post",
    jsonKeys: ["typeAuth"],
  },
  GETCharacterList: {
    url: "/charactersList",
    type: "get",
  },
  POSTCreateCharacter: {
    url: "/createCharacter",
    type: "post",
    jsonKeys: ["name", "password", "display_name"],
  },
  GETCharacterInfo: {
    url: "/characterInfo",
    type: "get",
    queryKeys: ["characterName"],
  },
  GETAbility: {
    url: "/abilityInfo",
    type: "get",
    queryKeys: ["abilityName", "characterName"],
  },
  GETAbilityDefensive: {
    url: "/defAbilityInfo",
    type: "get",
    queryKeys: ["abilityName", "characterName"],
  },
  GETTrinket: {
    url: "/trinketInfo",
    type: "get",
    queryKeys: ["trinketName", "characterName"],
  },
  GETWearable: {
    url: "/wearableInfo",
    type: "get",
    queryKeys: ["wearableName", "characterName"],
  },
};
