<template>
  <div class="desktop-labels-sidebar">
    <div class="labels-column">
      <router-link :to="{ name: 'Character' }" class="app-label">
        <img src="@/assets/images/entity-label.png" />
        <div>Сущность</div>
      </router-link>
      <!--TODO: надо признак надыбать какой-нибудь чтобы понимать, что человеку сюда можно и скрывать эту кнопку если низя-->
      <router-link :to="{ name: 'AdminPanel' }" class="app-label">
        <img src="@/assets/images/entity-app-label_mock.png" />
        <div>Редактор реальности</div>
      </router-link>
    </div>
    <div class="start-container">
      <div class="start-drop-panel panel" v-if="isStartMenuShown">
        <header>Привет, {{ $store.state.selectedCharacterName }}</header>
        <div @click="changeCharacter">
          <Icon icon="openmoji:counterclockwise-arrows-button" />
          Смена пользователя
        </div>
        <div @click="logOut"><Icon icon="openmoji:red-eye" /> Выключение</div>
      </div>
      <div
        class="start-button app-label"
        @click="isStartMenuShown = !isStartMenuShown"
      >
        <img src="@/assets/images/snack-start.png" />
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue";

export default {
  name: "DesktopLabelSidebar",
  components: { Icon },
  data() {
    return {
      isStartMenuShown: false,
    };
  },
  methods: {
    changeCharacter() {
      this.$store.commit("dumbMutation", {
        field: "selectedCharacterName",
        value: undefined,
      });
    },
    logOut() {
      this.$store.commit("dumbMutation", {
        field: "isAuthorized",
        value: false,
      });
      this.$router.push({ name: "Auth" });
    },
  },
};
</script>

<style lang="scss">
.desktop-labels-sidebar {
  height: 100vh;
  display: flex;
  flex-direction: column;

  .labels-column {
    flex: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    column-gap: 15px;
  }
  .app-label {
    width: 70px;
    color: white;
    margin: 15px;
    padding: 5px;
    font-weight: bold;
    cursor: pointer;

    > * {
      user-select: none;
    }

    img {
      width: 100%;
    }
  }

  .start-container {
    position: relative;
    .start-button {
      z-index: 2;
      position: relative;
      &:hover {
        background: radial-gradient(
          circle,
          rgba(255, 255, 255, 1) 30%,
          rgba(39, 198, 218, 1) 60%,
          rgba(255, 255, 255, 0) 71%
        );
      }
    }
    .start-drop-panel {
      z-index: 2;
      position: absolute;
      bottom: 50%;
      left: 50%;
      min-width: 215px;
      padding-bottom: 45px;

      header {
        padding: 10px;
        font-size: 14px;
        text-align: left;
        font-family: "Press Start 2P", cursive;
      }
      > div {
        font-size: 16px;
        font-family: "Roboto Mono", monospace;
        font-weight: bold;
        text-align: left;
        padding: 5px 10px;
        cursor: pointer;
        display: flex;
        align-items: center;
        gap: 5px;

        &:hover {
          background-color: rgba(46, 46, 46, 0.11);
        }
      }
    }
  }
}
</style>
